@mixin truncate($font-size: 1rem) {
  display: block;
  max-width: 100%;
  max-height: #{$font-size * 1.5}rem;
  font-size: #{$font-size}rem;
  line-height: 1.5;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
